import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
		data(){
			return{
				password:'',
				password1:''
			}
		},
		methods: {
			
			pass(){
				if (this.password.length < 6) {
					uni.showToast({
						icon: 'none',
						title: '密码最短为 6 个字符'
					});
					return;
				}
				if (this.password !== this.password1) {
					uni.showToast({
						icon: 'none',
						title: '俩次密码不一致'
					});
					return;
				}
				let that = this;
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/update/user/password',
					method: 'PUT',
					data: {
						id:this.userId,
						password:this.password
					},
					success: res => {
						console.log(res);
						uni.showToast({
							title:'修改成功'
						});
					},
					fail: () => {},
					complete: () => {}
				});
			},
		}
		
	}
